* {

    padding: 0;
    margin: 0;
    box-sizing: border-box;

}

@keyframes slide-in-left {
    0% {
        opacity: 0;
        transform: translateX(-100%);
    }
    100% {
        opacity: 1;
        transform: translateX(0);
    }
}
@keyframes slide-in-right {
    0% {
        opacity: 0;
        transform: translateX(100%);
    }
    100% {
        opacity: 1;
        transform: translateX(0);
    }
}

@keyframes slide-in-bottom {
    0% {
        opacity: 0;
        transform: translateY(100%);
    }
    100% {
        opacity: 1;
        transform: translateY(0);
    }
}

.fade-in {
    opacity: 1 !important;
}



.animated-element {
    animation-fill-mode: forwards;
}

.no-scroll {
    overflow: hidden;
}

section {

    min-height: 50vh;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: rgba(0,0,0,0);
    margin-top: -160px;

}

.container {

    width: 90%;
    max-width: 1000px;
    margin: 0 auto;
    display: flex;
    align-items: center;
    justify-content: center;

}

.left {

    width: 400px;
    height: 400px;
    border-radius: 50%;
    background: url("https://i.pinimg.com/originals/0c/0f/ae/0c0fae102f84e893c06996ce3b415e8e.png") no-repeat center / cover ;

}

.right {

    width: 50%;
    min-height: 20px;
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 30px;
    color: white;

}

.horizontal-container {

    display: flex;
    justify-content: center;
    align-items: center;
    width: 95%;
    bottom: 64px;
    right: -32px;
    left: 32px;
    position: absolute;

}

.outlined-button {

    background-color: transparent;
    color: gray;
    border: 2px solid gray;
    padding: 10px 20px;
    font-size: 16px;
    width: 40%;
    margin-bottom: 0;
    margin-right: 32px;
    border-radius: 12px;
    -webkit-transition-duration: 0.4s;
    transition-duration: 0.4s;

}

.outlined-button:hover {

    color: white;
    border: 2px solid white;

}

.filled-button {

    border: none;
    background-color: white;
    padding: 14px 20px;
    font-size: 16px;
    width: 40%;
    color: #111111;
    border-radius: 12px;
    -webkit-transition-duration: 0.4s;
    transition-duration: 0.4s;

}

.filled-button:hover { background-color: #8770c0; }

@media screen and (max-height: 771px) {

    .left {

        width: 50vh;
        height: 50vh;


    }
    .filled-button {

        padding: 10px 8px;
        font-size: 12px;
        width: 40%;
        border-radius: 10px;
        -webkit-transition-duration: 0.4s;
        transition-duration: 0.4s;

    }

    .outlined-button {

        border: 2px solid gray;
        padding: 8px 8px;
        font-size: 12px;
        width: 40%;
        margin-bottom: 0;
        border-radius: 10px;
        -webkit-transition-duration: 0.4s;
        transition-duration: 0.4s;

    }

    .horizontal-container {
        bottom: 5vh;
    }


}
@media screen and (max-height: 771px) and (max-width: 768px) {

    .left {

        width: 125px;
        height: 125px;
        margin-top: 30%;
        border-radius: 50%;
        animation: 'gradientAnimation 1s ease-in-out infinite';

    }

    .right {

        width: 50%;
        min-height: 20px;
        display: flex;
        align-items: center;
        justify-content: center;
        padding: 30px;
        color: white;

    }

    .filled-button {

        padding: 10px 16px;
        font-size: 12px;
        width: 40%;
        border-radius: 10px;
        -webkit-transition-duration: 0.4s;
        transition-duration: 0.4s;

    }

    .horizontal-container {
        bottom: 20px;
    }

    .outlined-button {

        border: 2px solid gray;
        padding: 8px 16px;
        font-size: 12px;
        width: 40%;
        margin-bottom: 0;
        border-radius: 10px;
        -webkit-transition-duration: 0.4s;
        transition-duration: 0.4s;

    }
}

@media screen and (max-width: 768px) and (min-height: 600px) {
    .left {
        width: 200px;
        height: 200px;
    }
}

@media screen and (max-width: 768px) {

    .section {
        min-height: 180vh;
    }

    .container {
        flex-direction: column;
        margin-top: -60px;
    }

    .horizontal-container {
        flex-direction: column;
        left: 12px;
    }

    .outlined-button {
        margin-right: 0;
        margin-bottom: 16px;
    }


    .right {
        width: 100%;
    }

    .outlined-button {
        width: 90%;
    }

    .filled-button {
        width: 90%;
    }

}

